<template>
	<div id="gotop">
		<div class="btn"
			 v-show='show'
			 :style="{opacity:opacity,top:top,right:right,bottom:bottom,left:left}"
			 @click='scroll_To(scrollTo)'>
			<i class="iconfont  icon-fanhuidingbu"></i>
		</div>
	</div>
</template>

<script>
	export default{
		data(){
		    return{
		      show:false,
		      onscroll:false,
		      opacity:this.staticOpacity
		    }
		},
		props:{
			//按下goTop后滚动的目标位置
			scrollTo:{
				type:Number,
				default:0
			},
			//滚动screenNum个屏幕高度后出现goTop按钮
			screenNum:{
				type:Number,
				default:4
			},
			scrollOpacity:{
				type:Number,
				default:0.1
			},
			staticOpacity:{
				type:Number,
				default:0.8
			},
			//位置参数4个
			top:{
				type:String
			},
			right:{
				type:String,
				default:'0px'
			},
			bottom:{
				type:String,
				default:'50px'
			},
			left:{
				type:String
			}
		},
		ready:function(){
		    window.onscroll=()=>{
		    //滚动时半透明
		      this.opacity=this.scrollOpacity;
		      //静止后不透明
		      let old=document.documentElement.scrollTop||document.body.scrollTop;
		      let check=()=>{
		      	if(document.body.scrollTop||document.documentElement.scrollTop==old){
		      		this.opacity=this.staticOpacity;
		      	}
		      }
		      setTimeout(check,800)
		      //滚动到的scrolltop显示
		      if(document.documentElement.scrollTop||document.body.scrollTop>window.screen.height*this.screenNum){
		        this.show=true
		      }else{this.show=false}
		    }
		},
		methods:{
			scroll_To:function (tar_y){ 
			    var timer = setTimeout(()=>{  
			        var current_y = document.body.scrollTop||document.documentElement.scrollTop;
			        var step = 40;//步长系数 即剩余的距离除以40 每1ms 移动一段距离  
			        if (tar_y>current_y){ //tar_y > current_y 即向下滚动  
			            var dist = Math.ceil((tar_y-current_y)/step)  
			            var next_y = current_y+dist  
			            if(next_y<tar_y){    
			                window.scrollTo(0,next_y);    
			                scroll_To(tar_y);
			            }  
			            else{  
			                window.scrollTo(0,tar_y)  
			            }  
			        }  
			        else{ //tar_y < current_y 即向上滚动  
			            var dist = Math.floor((tar_y-current_y)/step)  
			            var next_y = current_y+dist  
			            if(next_y>tar_y){  
			                window.scrollTo(0,next_y)  
			                this.scroll_To(tar_y)  
			            }  
			            else{  
			                window.scrollTo(0,tar_y)  
			            }  
			        }  
			    },1)  
			}  

		}
	}
	
	
	
</script>

<style scoped lang='scss'>
	.btn{
		width:.8rem;
		height:.8rem;
		line-height: .9rem;
		position: fixed;
		margin: 10px;
		border-radius: 50%;
		background-color: #666666;
		transition: all .5s ease;
		color: #fff;
		i{
			font-size: .5rem;
		}
	}
	
</style>


